"use client";

import { motion, AnimatePresence } from "framer-motion";
import { Card, CardContent, CardHeader, CardTitle } from "./ui/card";
import { cn } from "@/lib/utils";

type Player = {
  id?: string;
  name: string;
  score: number;
};

export function Leaderboard({
  players,
  currentPlayerId,
}: {
  players: Player[];
  currentPlayerId?: string | null;
}) {
  // sort theo điểm
  const sorted = [...players].sort((a, b) => b.score - a.score);

  // tính rank (xử lý đồng hạng)
  let lastScore: number | null = null;
  let lastRank = 0;

  const ranked = sorted.map((p, index) => {
    const rank =
      p.score === lastScore ? lastRank : index + 1;

    lastScore = p.score;
    lastRank = rank;

    return { ...p, rank };
  });

  return (
    <Card className="border-2 border-primary/20 shadow-xl">
      <CardHeader>
        <CardTitle>Bảng xếp hạng</CardTitle>
      </CardHeader>

      <CardContent className="space-y-2 max-h-96 overflow-y-auto">
        <AnimatePresence>
          {ranked.map((p) => {
            const isMe = p.id === currentPlayerId;

            return (
              <motion.div
                key={p.id || p.name}
                layout
                initial={{ opacity: 0, y: 10 }}
                animate={{ opacity: 1, y: 0 }}
                exit={{ opacity: 0 }}
                transition={{ type: "spring", stiffness: 300, damping: 25 }}
                className={cn(
                  "flex justify-between items-center px-4 py-2 rounded",
                  "bg-muted/30",
                  isMe &&
                    "bg-primary/20 border border-primary shadow-md"
                )}
              >
                <span className="font-medium">
                  #{p.rank} {p.name}
                  {isMe && " (Bạn)"}
                </span>
                <span className="font-bold">
                  {p.score} điểm
                </span>
              </motion.div>
            );
          })}
        </AnimatePresence>
      </CardContent>
    </Card>
  );
}
