"use client";

import { useState, useEffect } from "react";
import GameCard from "./game-card";
import { CardType } from "@/types/card";
import { calculateScore } from "@/utils/calculateScore";
import { applyCardEffect } from "@/utils/applyCardEffect";
import { getPlayerScore, updateScore } from "@/utils/firebase";
import { randomCards } from "@/utils/randomCards";

type Phase = "question" | "card";

interface Question {
  question: string;
  options: string[];
  answer: string;
}

const QUESTIONS: Question[] = [
  {
    question: "Đại hội IX của Đảng xác định mô hình kinh tế tổng quát là gì?",
    options: [
      "Kinh tế kế hoạch hóa tập trung",
      "Kinh tế thị trường định hướng XHCN",
      "Kinh tế thị trường hiện đại",
      "Kinh tế nhiều thành phần",
    ],
    answer: "Kinh tế thị trường định hướng XHCN",
  },
  {
    question: "Đại hội VIII xác định chiến lược nào?",
    options: [
      "Công nghiệp hóa – hiện đại hóa",
      "Phát triển nông nghiệp",
      "Mở cửa hội nhập",
      "Kinh tế tri thức",
    ],
    answer: "Công nghiệp hóa – hiện đại hóa",
  },
];

function shuffle<T>(arr: T[]) {
  return [...arr].sort(() => Math.random() - 0.5);
}

export default function GamePlay({
  playerId,
  roomCode,
}: {
  playerId: string;
  roomCode: string;
}) {
  /* ===== CORE STATE ===== */
  const [phase, setPhase] = useState<Phase>("question");
  const [score, setScore] = useState(0);

  /* ===== QUESTION ===== */
  const [questions, setQuestions] = useState<Question[]>(shuffle(QUESTIONS));
  const [currentIdx, setCurrentIdx] = useState(0);
  const currentQuestion = questions[currentIdx];

  const [selectedOption, setSelectedOption] = useState<string | null>(null);
  const [timeLeft, setTimeLeft] = useState(15);

  /* ===== CARD ===== */
  const CARD_COUNT = 3;
  const MAX_FLIP = 2;

  const [cardOptions, setCardOptions] = useState<CardType[]>([]);
  const [cardsFlipped, setCardsFlipped] = useState<boolean[]>(
    Array(CARD_COUNT).fill(false)
  );
  const [cardFlipsLeft, setCardFlipsLeft] = useState(MAX_FLIP);

  /* ===== TIMER ===== */
  useEffect(() => {
    if (phase !== "question" || selectedOption) return;

    if (timeLeft <= 0) {
      setSelectedOption("");
      setTimeout(nextQuestion, 800);
      return;
    }

    const timer = setTimeout(() => setTimeLeft((v) => v - 1), 1000);
    return () => clearTimeout(timer);
  }, [timeLeft, phase, selectedOption]);

  /* ===== ANSWER ===== */
  const handleAnswer = (option: string) => {
    if (selectedOption) return;

    setSelectedOption(option);
    const isCorrect = option === currentQuestion.answer;

    if (!isCorrect) {
      setTimeout(nextQuestion, 1200);
      return;
    }

    const gained = calculateScore(true, timeLeft);
    setScore((prev) => {
      const updated = prev + gained;
      updateScore(roomCode, playerId, updated);
      return updated;
    });

    // Reset card phase
    setCardOptions(randomCards(CARD_COUNT));
    setCardsFlipped(Array(CARD_COUNT).fill(false));
    setCardFlipsLeft(MAX_FLIP);
    setPhase("card");
  };

  /* ===== CARD CLICK ===== */
  const handleCardClick = async (idx: number) => {
    if (cardsFlipped[idx] || cardFlipsLeft <= 0) return;

    setCardsFlipped((prev) =>
      prev.map((v, i) => (i === idx ? true : v))
    );
    setCardFlipsLeft((v) => v - 1);

    await applyCardEffect({
      roomCode,
      playerId,
      card: cardOptions[idx],
    });

    const updatedScore = await getPlayerScore(roomCode, playerId);
    if (updatedScore !== null) setScore(updatedScore);

    if (cardFlipsLeft - 1 <= 0) {
      setTimeout(nextQuestion, 900);
    }
  };

  /* ===== NEXT QUESTION ===== */
  const nextQuestion = () => {
    setSelectedOption(null);
    setPhase("question");
    setTimeLeft(15);
    setCardOptions([]);

    if (currentIdx + 1 < questions.length) {
      setCurrentIdx((v) => v + 1);
    } else {
      setQuestions(shuffle(QUESTIONS));
      setCurrentIdx(0);
    }
  };

  return (
    <div className="mx-auto max-w-2xl space-y-6 text-center">
      <p className="text-xl font-bold">Điểm: {score}</p>

      {/* ===== QUESTION PHASE ===== */}
      {phase === "question" && (
        <div className="p-6 border rounded-xl bg-white shadow">
          <h2 className="text-xl font-semibold mb-2">
            {currentQuestion.question}
          </h2>
          <p className="text-sm text-gray-500 mb-4">
            Thời gian còn lại: {timeLeft}s
          </p>

          <div className="grid grid-cols-2 gap-4">
            {currentQuestion.options.map((opt) => {
              const isCorrect = opt === currentQuestion.answer;
              let style = "bg-gray-100";

              if (selectedOption) {
                if (opt === selectedOption) {
                  style = isCorrect
                    ? "bg-green-500 text-white"
                    : "bg-red-500 text-white";
                } else if (isCorrect) {
                  style = "bg-green-500 text-white";
                }
              }

              return (
                <button
                  key={opt}
                  className={`p-3 rounded font-medium ${style}`}
                  onClick={() => handleAnswer(opt)}
                  disabled={!!selectedOption}
                >
                  {opt}
                </button>
              );
            })}
          </div>
        </div>
      )}

      {/* ===== CARD PHASE ===== */}
      {phase === "card" && (
        <div className="space-y-4">
          <h2 className="text-xl font-bold">🎴 Chọn bài</h2>

          <div className="flex justify-center gap-6">
            {cardOptions.map((type, idx) => (
              <GameCard
                key={idx}
                type={type}
                flipped={cardsFlipped[idx]}
                onClick={() => handleCardClick(idx)}
              />
            ))}
          </div>

          <p className="text-sm text-gray-500">
            Lượt lật còn lại: {cardFlipsLeft}
          </p>
        </div>
      )}
    </div>
  );
}
