import { CardType } from "@/types/card";
import { getPlayerScore, updateScore } from "./firebase";

interface ApplyCardParams {
  roomCode: string;
  playerId: string;
  card: CardType;
}

export async function applyCardEffect({
  roomCode,
  playerId,
  card,
}: ApplyCardParams) {
  const score = await getPlayerScore(roomCode, playerId);
  if (score === null) return;

  let newScore = score;

  switch (card) {
    case "+50":
      newScore += 50;
      break;
    case "+100":
      newScore += 100;
      break;
    case "+150":
      newScore += 150;
      break;
    case "-50":
      newScore -= 50;
      break;
    case "-100":
      newScore -= 100;
      break;
    case "-150":
      newScore -= 150;
      break;
    case "x2":
      newScore *= 2;
      break;
    case "x3":
      newScore *= 3;
      break;
    case "loseAll":
      newScore = 0;
      break;

    case "steal20":
    case "steal50":
    case "swap":
      return;
  }

  await updateScore(roomCode, playerId, newScore);
}
